body{
  overflow-x: hidden;
}
.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}

.App-link {
  color: #61dafb;
}
.active_class{
  color:yellow;
  border-bottom:20px;
}
@media (max-width: 768px) {
  #priceTable{
    overflow-x: scroll;
  }
}
@media (min-width: 769px) {
  #priceTable{
    overflow-x: hidden;
  }
}

.blogOddContent{
  background-color:rgb(255, 125, 175);
  margin:-20px -10px 20px 20px;
}

.blogOddContent:hover{
  background-color:rgb(255, 125, 175);
  margin:20px 10px -20px -20px;
  cursor: pointer;
}
.blogEvenContent{
  background-color:rgb(255, 125, 175);
  margin:20px 10px -20px -20px;
}

.blogEvenContent:hover{
  background-color:rgb(255, 125, 175);  
  margin:-20px -10px 20px 20px;
  cursor: pointer;
}

/* .......................................  */
.rest {
  box-shadow: 0 0 5px grey;
  border-radius: 5px;
  margin: 10px;
  width: 450px;

  font-family: touse;

  transition-property: box-shadow;
  transition-duration: 0.25s;
}

.rest:hover {
  box-shadow: 0 0 15px grey;
}

.sepline {
  height: 1px;
  width: 100%;
  background-color: rgb(230, 230, 230);
  margin-top: 5px;
}

.restinfo {
  padding: 10px;
  cursor: default;
}

.restcity {
  font-weight: bold;
  font-size: 12px;
}

.restname {
  font-size: 20px;
}

.restcuisines {
  font-size: 15px;
}

.restcuisine {
  margin: 2px;
  border: 1px solid grey;
}

.restfilter {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-family: touse;
  padding: 10px;
}

.reststats {
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  padding: 10px;
  cursor: default;

  transition-property: background-color, color;
  transition-duration: 0.25s;
}

.reststats:hover {
  background-color: cornflowerblue;
  color: white;
}
.bg-pink-button{
  color:white;
  background-image:linear-gradient( to right,#fd6581,#ff5978);
}
.bg-yellow-button{
  color:black;
  background-image:linear-gradient( to right,#f8c220, #c39509);
}

/* Home Page */
@media (max-width: 768px) {
  .home-header-bg{
    height: auto;
    background-image: linear-gradient(to right,rgb(54,44,157),rgb(14, 6, 68));
    padding-left: 10%;
  }
  .home-upper-section{
    margin-top: -30px !important;
    margin-bottom: -30px !important;
    z-index: 1;
  }  
}
@media (min-width: 769px) {
  .home-header-bg{
    height: 100vh;
    background-image: url("./img/Homepage-slider-image.jpeg");
    background-size:120%;
    background-position:30% 0%;
  }  
  .home-upper-section{
    margin-top: -100px !important;
    margin-bottom:-100px !important;
    z-index: 1;
  }  
}

