/* Feature Page*/


.bg-article-light{
  background-color: white;
}
.bg-article-dark{
  background-color: #ededed;
}
@media screen and (max-width: 769px) {
  .article-item-heading{
    font-size:34px;
    font-family:Montserrat-ExtraBold ;
  }
  .feature-header-bg{
    height: auto;
    background-image: linear-gradient(to right,rgb(54,44,157),rgb(14, 6, 68));
    padding-left: 10%;
  }
  .feature-header-heading{  
    font-size: 35px;
  }
}

@media screen and (min-width: 768px) {
  .article-item-heading{
    font-size:44px;
    font-family:Montserrat-ExtraBold ;
  }
  .feature-header-bg{
    height: 100vh;
    background-image: url("./img/Features-slider-image.jpeg");
    background-size:120%;
    background-position:30% 10%;
  }  
  .feature-header-heading{
  font-size: 40px;    
  }
  
}
